.pb-zoom-to-extent-ctl {
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  background: #fff;

  > button {
    width: 30px;
    height: 30px;
    display: block;
    padding: 0;
    outline: none;
    border: 0;
    box-sizing: border-box;
    background-color: transparent;
    cursor: pointer;
    background-image: url(./earth.svg);
    background-size: 80%;
    background-position: center;
    background-repeat: no-repeat;
  }
}
