.pb-trading-map {
  .pb-sidebar {
    z-index: 1001;

    .MuiDrawer-paper {
      height: calc(100% - 40px);
      overflow: hidden;
    }
  }

  .pb-feature-info {
    z-index: 1020;
    position: absolute;
    right: 220px;
    bottom: 50px;
  }

  .pb-map-legend {
    z-index: 1020;
    position: absolute;
    right: 10px;
    bottom: 50px;
  }

  .pb-total-info {
    z-index: 1020;
    position: absolute;
    left: 422px;
    top: 10px;
  }

  .pb-snackbar-feedback {
    z-index: 2000;
  }

  .pb-share-button {
    z-index: 1010;
    position: absolute;
    top: 15px;

    circle {
      fill: #b0b0b0;
    }

    &.twitter {
      :hover {
        circle {
          fill: rgb(0, 172, 237);
        }
      }
      right: 70px;
    }

    &.facebook {
      :hover {
        circle {
          fill: rgb(59, 89, 152);
        }
      }
      right: 120px;
    }
  }
}
